import { Component, OnInit } from "@angular/core";
import { SweetalertComponent } from "app/shared/sweetalert/sweetalert.component";
import { OrderService } from "../../service/order.service";
import { ReportService } from "app/modules/reports/services/report.service";
import { orderInvoiceReport } from "app/shared/reports/customerOrderInvoice.service";
import { Router } from "@angular/router";
import { measurementInvoiceReport } from "app/shared/reports/measurement.service";

@Component({
  selector: "app-order-list",
  templateUrl: "./order-list.component.html",
  styleUrls: ["./order-list.component.scss"],
})
export class ListOrderComponent implements OnInit {
  public contentHeader: Object;
  public tableDataArray = [];

  constructor(
    private _sw: SweetalertComponent,
    private _serviceData: OrderService,
    private reportService: ReportService,
    private orderInvoice: orderInvoiceReport,
    private _measurementInvoice: measurementInvoiceReport,
    private _router: Router
  ) {}

  ngOnInit(): void {
    this.getAllDetail();

    this.contentHeader = {
      headerTitle: "Customer",
      actionButton: true,
      breadcrumb: {
        type: "",
        links: [
          {
            name: "Order",
            isLink: true,
            link: "/",
          },
          {
            name: "List",
            isLink: false,
          },
        ],
      },
    };
  }

  getAllDetail() {
    this._serviceData.getAll().subscribe((res) => {
      if (res.data) {
        this.tableDataArray = res.data;
      } else if (!res.data && this.tableDataArray.length == 1) {
        this.tableDataArray = [];
      }
    });
  }

  confirmDelete(id, name, phone) {
    let deleteId = {
      id: id,
      customer_name: name,
      customer_phone: phone,
    };
    this._sw.delete(() =>
      this._serviceData.delete(deleteId).subscribe(
        (res) => {
          setTimeout(() => this.getAllDetail(), 2000);
        },
        (err) => {
          this._sw.fire("error", "Collection", "There is a problem");
        }
      )
    );
  }

  searchTerm: string = "";
  get filterTableData() {
    if (!this.searchTerm) {
      return this.tableDataArray;
    }
    const searchTermLowerCase = this.searchTerm.toLowerCase();

    return this.tableDataArray.filter(
      (item) =>
        item.customer_name.toLowerCase().includes(searchTermLowerCase) ||
        item.customer_phone.toLowerCase().includes(searchTermLowerCase) ||
        item.order_date.toLowerCase().includes(searchTermLowerCase)
    );
  }

  makeReport(orderId) {
    this.reportService.orderInvoiceReport({orderId:orderId}).subscribe((getDetail) => {
      if(getDetail.data){
        this.orderInvoice.generatePdfContent(getDetail.data)
          this._router.navigate(['/reports/view']);        
      }
    });
  }

  measurementReport(orderId) {
    this.reportService.orderInvoiceReport({orderId:orderId}).subscribe((getDetail) => {
      if(getDetail.data){
        this._measurementInvoice.generatePdfContent(getDetail.data)
          this._router.navigate(['/reports/view']);        
      }
    });
  }  
}
