import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { OrderFormComponent } from './component/order-form/order-form.component';
import { RouterModule, Routes } from '@angular/router';
import { ContentHeaderModule } from 'app/layout/components/content-header/content-header.module';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { CoreCommonModule } from '@core/common.module';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { NgSelectModule } from '@ng-select/ng-select';
import { SweetalertComponent } from 'app/shared/sweetalert/sweetalert.component';
import { ListOrderComponent } from './component/list-order/order-list.component';
import { SharedModule } from 'app/shared/shared.module';

const routes: Routes = [
  {
    path: "",
    component: ListOrderComponent,
    data: { animation: "grn-form" },
  },
   {
    path: "list",
    component: ListOrderComponent,
    data: { animation: "grn-form-list" },
  },
  {
    path: "form",
    component: OrderFormComponent,
    data: { animation: "order-form" },
  },
  {
    path: "form/:id",
    component: OrderFormComponent,
    data: { animation: "order-form-id" },
  },
]

@NgModule({
  declarations: [
    OrderFormComponent,
    ListOrderComponent
  ],
  imports: [
    CommonModule,
    ContentHeaderModule,
    RouterModule.forChild(routes),
    FormsModule,
    CoreCommonModule,
    NgbModule,
    NgSelectModule,
    ReactiveFormsModule,
    SharedModule
   ],
   providers: [SweetalertComponent],

})
export class OrderModule { }
