import { Component, OnInit, ViewEncapsulation } from "@angular/core";
import { FormArray, FormBuilder, FormGroup, Validators } from "@angular/forms";
import { ActivatedRoute, Router } from "@angular/router";
import { CoreConfigService } from "@core/services/config.service";
import { NgbDateStruct, NgbModal } from "@ng-bootstrap/ng-bootstrap";
import { CollarTypeService } from "app/modules/design/service/collarType.service";
import { CuffTypeService } from "app/modules/design/service/cuffType.service";
import { PlacketTypeService } from "app/modules/design/service/placketType.service";
import { StichTypeService } from "app/modules/design/service/stichType.service";
import { ItemMasterService } from "app/modules/item-master/service/itemMaster.service";
import { UserService } from "app/modules/user/service/user.service";
import { ModalAddCustomerComponent } from "app/shared/modal-add-customer/modal-add-customer.component";
import { Subject } from "rxjs";
import { takeUntil } from "rxjs/operators";
import { OrderService } from "../../service/order.service";
import { SweetalertComponent } from "app/shared/sweetalert/sweetalert.component";
import { ImageModalComponent } from "app/shared/image-modal/image-modal.component";

@Component({
  selector: "app-order-form",
  templateUrl: "./order-form.component.html",
  styleUrls: ["./order-form.component.scss"],
  encapsulation: ViewEncapsulation.None,
})
export class OrderFormComponent implements OnInit {
  contentHeader: object;
  public orderForm: FormGroup;
  public submitted = false;
  public editable = false;
  public id;
  public userDetail;
  public itemMaster;
  public stichType;
  public cuffType;
  public collarType;
  public placketType;
  public received_amount = 0;
  public damanType = ["Square", "Round"];
  public orderType = ["Pending", "Processing", "Complete"];
  public zipType = ["Zip Pocket", "Trouser Pocket", "Simple Pocket"];
  public orderAmount = {
    previous_balance: null,
    total_amount: null,
    net_balance: null,
    received_amount: null,
  };
  public items = [];
  public customerData = {
    customerName: null,
    customerNumber: null,
  };
  public emptyMeasurementValue = {
    length: "",
    shoulder: "",
    sleeve: "",
    chest: "",
    chest_loosing: "",
    middle: "",
    middle_loosing: "",
    west: "",
    west_loosing: "",
    hip: "",
    hip_loosing: "",
    daman: "",
    daman_loosing: "",
    daman_type: null,
    cuff_type: null,
    cuff_type_size: "",
    collar: "",
    collar_type: null,
    collar_type_size: "",
    placket_type: null,
    placket_type_size: "",
    front_pocket: "",
    side_pocket: "",
    right_length: "",
    right_west: "",
    right_hip: "",
    thai: "",
    knee: "",
    calf: "",
    bottom: "",
  };
  // multiple Image Upload
  public orderImages = [];
  public editOrderImage = [];
  onFileChange(event, index) {
    if (event.target.files && event.target.files[0]) {
      this.items[index].itemImage = event.target.files[0].name;
      this.orderForm.controls.details.value[index].itemImage =
        event.target.files[0].name;
      let reader = new FileReader();
      reader.onload = (event: any) => {
        this.editOrderImage[index] = event.target.result;
        this.orderImages[index] = event.target.result;
      };
      reader.readAsDataURL(event.target.files[0]);
    }
  }

  imagesUpload() {
    const multiImage = {
      imageName: this.items.map((name) => name.itemImage),
      file: this.orderImages,
      filePath: "order",
    };
    // const allNull = multiImage.imageName.every((element) => element === null);
    // if (!allNull) {
    this._orderService.uploadMultipleImage(multiImage).subscribe();
    // }
  }

  constructor(
    private _itemMaster: ItemMasterService,
    private _customerService: UserService,
    private _stichTypeService: StichTypeService,
    private _cuffTypeService: CuffTypeService,
    private _collarTypeService: CollarTypeService,
    private _placketTypeService: PlacketTypeService,
    private _orderService: OrderService,
    private fb: FormBuilder,
    private _router: Router,
    private modalService: NgbModal,
    private route: ActivatedRoute,
    private _sw: SweetalertComponent
  ) {}

  onSubmit(fromValue) {
    this.submitted = true;
    if (this.orderForm.invalid) {
      return;
    }

    this.imagesUpload();
    if (this.editable) {
      this.updateFormDetail(fromValue);
    } else {
      this.insertFormDetail(fromValue);
    }
  }

  insertFormDetail(formData) {
    this._orderService.create(formData).subscribe(
      (res) => {
        this._sw.fire("success", "Order Invoice", `Record Successfully Add..`);
        this.submitted = false;
        this.orderImages = [];
        this.editOrderImage = [];
        this.orderForm.reset();
      },
      (err) => {
        this._sw.fire("error", "Item Master", err.error.message);
      }
    );
  }

  updateFormDetail(formData) {
    this._orderService.update({ id: this.id, ...formData }).subscribe(
      (res) => {
        this._sw.fire(
          "success",
          "Order Invoice",
          `Record Successfully Update..`
        );
        this.editable = false;
        this.submitted = false;
        this._router.navigate(["/order"]);
      },
      (err) => {
        this._sw.fire("error", "Item Master", err.error.message);
      }
    );
  }
   
  makeReactiveForm() {
    this.orderForm = this.fb.group({
      customer_name: [null],
      customer_phone: [null],
      order_type: ["Pending"],
      item_master: [null],
      previous_balance: [{ value: "", disabled: true }],
      person_name: [""],
      order_date: [new Date().toISOString().substring(0, 10)],
      plan_date: [new Date().toISOString().substring(0, 10)],
      actual_date: [new Date().toISOString().substring(0, 10)],
      total_amount: [""],
      amount_received: [""],
      balance_amount: [""],
      length: [""],
      shoulder: [""],
      sleeve: [""],
      chest: [""],
      chest_loosing: [""],
      middle: [""],
      middle_loosing: [""],
      west: [""],
      west_loosing: [""],
      hip: [""],
      hip_loosing: [""],
      daman: [""],
      daman_loosing: [""],
      daman_type: [null],
      cuff_type: [null],
      cuff_type_size: [""],
      collar: [""],
      collar_type: [null],
      collar_type_size: [""],
      placket_type: [null],
      placket_type_size: [""],
      front_pocket: [""],
      side_pocket: [""],
      right_length: [""],
      right_west: [""],
      right_hip: [""],
      thai: [""],
      knee: [""],
      calf: [""],
      bottom: [""],
      note: [""],
      rightZip: [""],
      details: this.fb.array([]),
    });
    if (!this.editable) {
      for (let i = 0; i < 3; i++) {
        this.addItem();
      }
    }
  }

  addItem() {
    const itemsArray = this.orderForm.get("details") as FormArray;
    this.items.push(this.createItemFormGroup().value);
    itemsArray.push(this.createItemFormGroup());
  }

  // Create a FormGroup for an item
  createItemFormGroup() {
    return this.fb.group({
      itemName: [null, [Validators.required]],
      itemQuantity: [""],
      itemPrice: [""],
      itemAmount: [""],
      itemNote: [""],
      itemImage: [null],
    });
  }

  // Get Details a FormGroup for an item
  getDetailFormGroup(item, i) {
    const detailFormGroup = this.fb.group({
      id: [item.id],
      itemName: [item.stich_type],
      itemQuantity: [item.quantity],
      itemPrice: [item.price],
      itemAmount: [item.amount],
      itemNote: [item.note],
      itemImage: [item.imageName],
    });
    this.editOrderImage[i] =
      item.imageName == null ? null : `assets/images/order/${item.imageName}`;
    (this.orderForm.get("details") as FormArray).push(detailFormGroup);
    this.items.push(detailFormGroup.value);
  }

  ngOnInit(): void {
    this.id = +this.route.snapshot.paramMap.get("id");
    if (this.id > 0) {
      this.editable = true;
      this.getDetailById();
    }
    this.makeReactiveForm();
    this.getUserDetails();
    this.getItemMaster();
    this.getStichType();
    this.getCuffType();
    this.getCollarType();
    this.getPlacketType();

    this.contentHeader = {
      headerTitle: "Invoice",
      actionButton: true,
      breadcrumb: {
        type: "",
        links: [
          {
            name: "Order",
            isLink: true,
            link: "/",
          },
          {
            name: "Form",
            isLink: false,
          },
        ],
      },
    };
  }

  getDetailById() {
    this._orderService.getDetailById({ id: this.id }).subscribe((res) => {
      this.orderForm.patchValue({
        customer_name: res.data.customer_name,
        customer_phone: res.data.customer_phone,
        order_type: res.data.order_type,
        item_master: res.data.item_master,
        person_name: res.data.person_name,
        order_date: res.data.order_date,
        plan_date: res.data.plan_date,
        actual_date: res.data.actual_date,
        total_amount: res.data.total_amount,
        amount_received: res.data.amount_received,
        balance_amount: res.data.balance_amount,
        length: res.data.length,
        shoulder: res.data.shoulder,
        sleeve: res.data.sleeve,
        chest: res.data.chest,
        chest_loosing: res.data.chest_loosing,
        middle: res.data.middle,
        middle_loosing: res.data.middle_loosing,
        west: res.data.west,
        west_loosing: res.data.west_loosing,
        hip: res.data.hip,
        hip_loosing: res.data.hip_loosing,
        daman: res.data.daman,
        daman_loosing: res.data.daman_loosing,
        daman_type: res.data.daman_type,
        cuff_type: res.data.cuff_type,
        cuff_type_size: res.data.cuff_type_size,
        collar: res.data.collar,
        collar_type: res.data.collar_type,
        collar_type_size: res.data.collar_type_size,
        placket_type: res.data.placket_type,
        placket_type_size: res.data.placket_type_size,
        front_pocket: res.data.front_pocket,
        side_pocket: res.data.side_pocket,
        right_length: res.data.right_length,
        right_west: res.data.right_west,
        right_hip: res.data.right_hip,
        thai: res.data.thai,
        knee: res.data.knee,
        calf: res.data.calf,
        bottom: res.data.bottom,
        rightZip: res.data.rightZip,
        note: res.data.note,
      });
      this.received_amount = res.data.amount_received;
      this.previousBalance(res.data.customer_name, res.data.customer_phone);
      res.data.details.forEach((item, i) => {
        this.getDetailFormGroup(item, i);
      });
    });
  }

  calculateTotalAmount(receivedAmount) {
    this.orderAmount.received_amount = this.received_amount + +receivedAmount;
    this.orderAmount.net_balance =
      +this.orderAmount.total_amount - +this.orderAmount.received_amount;
  }

  calculateAmount(i) {
    this.items[i].itemAmount =
      this.items[i].itemPrice * this.items[i].itemQuantity;
    this.sumTotalAmount();
  }

  sumTotalAmount() {
    let totalAmount = 0;
    for (const item of this.items) {
      if (item.itemAmount !== undefined) {
        totalAmount += item.itemAmount;
      }
    }
    this.orderAmount.total_amount = totalAmount;
    this.calculateTotalAmount(this.orderAmount.received_amount);
  }

  get details() {
    return this.orderForm.get("details") as FormArray;
  }

  deleteItem(id) {
    for (let i = 0; i < this.details.controls.length; i++) {
      if (this.details.controls.indexOf(this.details.controls[i]) === id) {
        this.details.value.splice(i, 1);
        this.details.controls.splice(i, 1);
        this.items.splice(i, 1);
        break;
      }
    }
  }

  get f() {
    return this.orderForm.controls;
  }

  navigateToBack() {
    this._router.navigate(["/order"]);
  }

  previousBalance(name, phone) {
    this._orderService
      .getCustomerTotalBalance({
        customer_name: name,
        customer_phone: phone,
      })
      .subscribe((res) => {
        if (res.data) {
          this.orderForm.patchValue({
            previous_balance: res.data.total_balance,
          });
        } else {
          this.orderForm.patchValue({
            previous_balance: "",
          });
        }
      });
  }

  selectCustomer(value) {
    this.customerData.customerName = value.full_name;
    this.customerData.customerNumber = value.phone;
    this.previousBalance(value.full_name, value.phone);
  }

  AddCustomer = () => {
    const openModel = this.modalService.open(ModalAddCustomerComponent, {
      centered: true,
      windowClass: "modal modal-primary",
      size: "xl",
    });
    openModel.closed.subscribe((close) => this.getUserDetails());
  };

  getUserDetails() {
    this._customerService.getAll().subscribe((res) => {
      if (res.data) {
        this.userDetail = res.data.filter(
          (type) => type.customerType == "Stitching"
        );
      }
    });
  }

  getStichType() {
    this._stichTypeService.getAll().subscribe((res) => {
      if (res.data) {
        this.stichType = res.data.filter((type) => type.status == 1);
      }
    });
  }

  getItemMaster() {
    this._itemMaster.getAll().subscribe((res) => {
      if (res.data) {
        debugger
        this.itemMaster = res.data.filter(
          (type) => type.itemType == "Stich"
        );;
      }
    });
  }

  getCuffType() {
    this._cuffTypeService.getAll().subscribe((res) => {
      if (res.data) {
        this.cuffType = res.data.filter((type) => type.status == 1);
      }
    });
  }

  getCollarType() {
    this._collarTypeService.getAll().subscribe((res) => {
      if (res.data) {
        this.collarType = res.data.filter((type) => type.status == 1);
      }
    });
  }

  getPlacketType() {
    this._placketTypeService.getAll().subscribe((res) => {
      if (res.data) {
        this.placketType = res.data.filter((type) => type.status == 1);
      }
    });
  }

  openImagePopup(i) {
    const openModel = this.modalService.open(ImageModalComponent, {
      centered: true,
      windowClass: "modal modal-primary",
      size: "lg",
    });
    openModel.componentInstance.modalHeader = "Order Image";
    openModel.componentInstance.imgSrc =
      this.editable == true ? this.editOrderImage[i] : this.orderImages[i];
  }

  getCustomerLastRecord() {
    let customerName = this.orderForm.value.customer_name;
    let personName = this.orderForm.value.person_name;
    if (customerName !== "" && personName !== "") {
      this._orderService
        .getCustomerLastRecord({
          customer_name: customerName,
          person_name: personName,
        })
        .subscribe((res) => {
          if (res.data.length > 0) {
            this.orderForm.patchValue({
              length: res.data[0].length,
              shoulder: res.data[0].shoulder,
              sleeve: res.data[0].sleeve,
              chest: res.data[0].chest,
              chest_loosing: res.data[0].chest_loosing,
              middle: res.data[0].middle,
              middle_loosing: res.data[0].middle_loosing,
              west: res.data[0].west,
              west_loosing: res.data[0].west_loosing,
              hip: res.data[0].hip,
              hip_loosing: res.data[0].hip_loosing,
              daman: res.data[0].daman,
              daman_loosing: res.data[0].daman_loosing,
              daman_type: res.data[0].daman_type,
              cuff_type: res.data[0].cuff_type,
              cuff_type_size: res.data[0].cuff_type_size,
              collar: res.data[0].collar,
              collar_type: res.data[0].collar_type,
              collar_type_size: res.data[0].collar_type_size,
              placket_type: res.data[0].placket_type,
              placket_type_size: res.data[0].placket_type_size,
              front_pocket: res.data[0].front_pocket,
              side_pocket: res.data[0].side_pocket,
              right_length: res.data[0].right_length,
              right_west: res.data[0].right_west,
              right_hip: res.data[0].right_hip,
              thai: res.data[0].thai,
              knee: res.data[0].knee,
              calf: res.data[0].calf,
              bottom: res.data[0].bottom,
              rightZip: res.data[0].rightZip,
            });
          } else {
            this.orderForm.patchValue(this.emptyMeasurementValue);
          }
        });
    }
  }

  deleteImageOrder(i) {
    this.orderForm.controls.details.value[i].itemImage = null;
    this.orderImages[i] = null;
    this.items[i].itemImage = null;
    this.editOrderImage[i] = null;
  }
}
