import { Injectable } from "@angular/core";
import { BehaviorSubject, Subject } from "rxjs";
import { reportViewInHtmlReport } from "./reportViewInHtml.service";
@Injectable({
  providedIn: "root",
})
export class measurementInvoiceReport {
  constructor(private _reportView: reportViewInHtmlReport) {}

  generatePdfContent(getDetail) {
    const makePdf = `<div class="card invoice-preview-card">
    <div class="divider">
    <div class="divider-text"><h3>CUSTOMER ORDER MEASUREMENT (TAILORING)</h3></div>
  </div>
      <div class="card-body invoice-padding py-1">
        <div class="d-flex justify-content-between">
          <div>
          <div class="invoice-date-wrapper mb-0">
          <p class="invoice-date">Invoice Number: </p>
          <h5 class="invoice-date-title ml-25">#NW-00${getDetail.id}</h5>
        </div>
          <div class="invoice-date-wrapper mb-0">
            <p class="invoice-date">Item Name: </p>
            <p class="invoice-date-title ml-25">${getDetail.item_master}</p>
          </div>
          </div>
          <div>
            <div class="invoice-date-wrapper mb-0">
              <p class="invoice-date">Order Date:</p>
              <p class="invoice-date-title ml-25">${getDetail.order_date}</p>
            </div>
            <div class="invoice-date-wrapper">
              <p class="invoice-date">Delivery Date:</p>
              <p class="invoice-date-title ml-25">${getDetail.actual_date}</p>
            </div>
          </div>
        </div>
      </div>
    <hr class="m-0" />
    <div class="card-body invoice-padding pt-0">
      <div class="row invoice-spacing">
        <div class="col-6 p-0">
          <table>
            <tbody>
              <tr>
                <td class="pr-1">Customer Name:</td>
                <td>${getDetail.customer_name}</td>
              </tr>
              <tr>
                <td class="pr-1">Person Name:</td>
                <td>${getDetail.person_name}</td>
              </tr>
              <tr>
                <td class="pr-1">Address:</td>
                <td>${getDetail.address}</td>
              </tr>
            </tbody>
          </table>
        </div>
        <div class="col-6 p-0">
          <div class="float-right">
            <table>
              <tbody>
                <tr>
                  <td class="pr-1">Mobile No:</td>
                  <td>${getDetail.customer_phone}</td>
                </tr>
                <tr>
                  <td class="pr-1">City:</td>
                  <td>${getDetail.city}</td>
                </tr>
                <tr>
                  <td class="pr-1">Email:</td>
                  <td>${getDetail.email}</td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </div>
    <div class="card-body py-0 row">
      <div class="col-8 border-right">
        <h4 class="text-center mt-25">TOP MEASUREMENTS</h4>
      </div>
      <div class="col-4">
        <h4 class="text-center mt-25">TROUSER / PANTS / SHALWAR</h4>
      </div>
      <div class="col-8 p-0 border-right">
        <div class="table-responsive">
          <table class="table">
            <tr>
              <th class="bg-light">Length</th>
              <td>${getDetail.length}</td>
              <th class="bg-light">Shoulder</th>
              <td>${getDetail.shoulder}</td>
            </tr>
            <tr>
              <th class="bg-light">Sleeve</th>
              <td>${getDetail.sleeve}</td>
              <th class="bg-light">Collar</th>
              <td>${getDetail.collar}</td>
            </tr>
            <tr>
              <th class="bg-light">Collar Type</th>
              <td>${getDetail.collar_type}</td>
              <th class="bg-light">Collar Type Size</th>
              <td>${getDetail.collar_type_size}</td>
            </tr>
            <tr>
              <th class="bg-light">Chest</th>
              <td>${getDetail.chest}</td>
              <th class="bg-light">Chest Loosing</th>
              <td>${getDetail.chest_loosing}</td>
            </tr>
            <tr>
              <th class="bg-light">Middle</th>
              <td>${getDetail.middle}</td>
              <th class="bg-light">Middle Loosing</th>
              <td>${getDetail.middle_loosing}</td>
            </tr>
            <tr>
              <th class="bg-light">West</th>
              <td>${getDetail.west}</td>
              <th class="bg-light">West Loosing</th>
              <td>${getDetail.west_loosing}</td>
            </tr>
            <tr>
              <th class="bg-light">Hip</th>
              <td>${getDetail.hip}</td>
              <th class="bg-light">Hip Loosing</th>
              <td>${getDetail.hip_loosing}</td>
            </tr>
            <tr>
              <th class="bg-light">Cuff</th>
              <td>${getDetail.cuff_type}</td>
              <th class="bg-light">Cuff Type Size</th>
              <td>${getDetail.cuff_type_size}</td>
            </tr>
            <tr>
              <th class="bg-light">Placket Type</th>
              <td>${getDetail.placket_type}</td>
              <th class="bg-light">Placket Type Size</th>
              <td>${getDetail.placket_type_size}</td>
            </tr>
            <tr>
              <th class="bg-light">Daman</th>
              <td>${getDetail.daman}</td>
              <th class="bg-light">Daman Loosing</th>
              <td>${getDetail.daman_loosing}</td>
            </tr>
            <tr>
              <th class="bg-light">Daman Type</th>
              <td>${getDetail.daman_type}</td>
              <th class="bg-light">Front Pocket</th>
              <td>${getDetail.front_pocket}</td>
            </tr>
            <tr>
              <th class="bg-light">Side Pocket</th>
              <td>${getDetail.side_pocket}</td>
              <th class="bg-light"></th>
              <td></td>
            </tr>
          </table>
        </div>
      </div>
      <div class="col-4 p-0">
        <table class="table">
          <tr>
            <th class="bg-light">Length</th>
            <td>${getDetail.right_length}</td>
          </tr>
          <tr>
            <th class="bg-light">West</th>
            <td>${getDetail.right_west}</td>
          </tr>
          <tr>
            <th class="bg-light">Hip</th>
            <td>${getDetail.right_hip}</td>
          </tr>
          <tr>
            <th class="bg-light">Thai</th>
            <td>${getDetail.thai}</td>
          </tr>
          <tr>
            <th class="bg-light">Knee</th>
            <td>${getDetail.knee}</td>
          </tr>
          <tr>
            <th class="bg-light">Calf</th>
            <td>${getDetail.calf}</td>
          </tr>
          <tr>
            <th class="bg-light">Bottom</th>
            <td>${getDetail.bottom}</td>
          </tr>
          <tr>
            <th class="bg-light">Zip Type</th>
            <td>${getDetail.rightZip}</td>
          </tr>
          <tr>
            <th class="bg-light">-</th>
            <td></td>
          </tr>
          <tr>
            <th class="bg-light">-</th>
            <td></td>
          </tr>
          <tr>
            <th class="bg-light">-</th>
            <td></td>
          </tr>
          <tr>
            <th class="bg-light">-</th>
            <td></td>
          </tr>
        </table>
      </div>
    </div>
    <div class="card-body invoice-padding pt-0">
      <div class="row">
        <div class="col-12" style="height: 100px">
          <span class="font-weight-bold">Note: </span>
          <span
            >${getDetail.note}</span
          >
        </div>
      </div>
    </div>
  </div>`;
    this._reportView.getPdfContent.next(makePdf);
  }
}
